import { useState, useEffect } from "react";
import { Axios } from "../Axios";
import Cookies from "js-cookie";
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import { Redirect } from 'react-router-dom';

export default function UserInfo_Edit () {

    //複数のuseEffectを使うときは各々state,useEffectの順番にする

    const [mydataList, setMydataList] = useState([]);

    const [id, setId] = useState(0)
    const [age, setAge] = useState(0);
    const [genre, setGenre] = useState('');
    const [address, setAddress] = useState('');
    const [artist, setArtist] = useState('');
    const [part, setPart] = useState('');
    const [image, setImage] = useState('');

    const [preview, setPreview] = useState('');


    useEffect(() => {
        const fetchData = async () => {
            const response = await Axios.get('views/mypageapi/', {
            'headers': {
                'X-CSRFToken': Cookies.get('csrftoken')
            }
            });
            const data = response.data
            setMydataList(response.data);
            ///ここではまだstateは変更されていない
            console.log(mydataList);
            var options = [
                "ギター",
                "ベース",
                "ドラム",
                "ボーカル",
                "キーボード"
            ];
            for(let i=0; i<data.length; i++) {
                console.log(data[i].age)
                //初期値をステート変数に格納
                setId(data[i].id)
                setAge(data[i].age);
                setGenre(data[i].genre);
                setAddress(data[i].address);
                setArtist(data[i].artist);
                setPart(data[i].part);
                var str=String(data[i].image)
                var img=str.replace('media/', '')
                console.log(img)
                setImage(img);
                setPreview(data[i].image);
                //選択肢に反映
                var genre_ = String(data[i].genre)
                const d = document.getElementById(genre_)
                d.setAttribute("selected", true)

                var parts = String(data[i].part)
            };

            for(let j=0; j<options.length; j++) {
                let option = options[j] 
                console.log(option)
                let re = new RegExp(`${option}`)
                let val=parts.match(re);
                console.log(val)
                try{
                    let p = String(val[0])
                    let part_=document.getElementById(p)
                    part_.setAttribute("checked", true)
                } catch(e) {
                    console.log('pass')
                }
                
            };
        };
        fetchData();
    },[]);

    console.log(genre)
    console.log(image)
    console.log(part)

/*
    useEffect(() => {
        mydataList.map((data) => {
        setAge(data.age);
        setGenre(data.genre);
        setAddress(data.address);
        setArtist(data.arist);
        setPart(data.part);
        setImage(data.image);
        console.log(data.image)
        });
        setAge(document.getElementById('age'));
        setGenre(document.getElementById('genre'));
        setAddress(document.getElementById('address'));
        setArtist(document.getElementById('artist'));
        setPart(document.getElementById('part'));
        const file = document.getElementById('image').files[0];
        if (file !== {}) {
            setImage(file)
            console.log('set')
        }
        else {
            setImage('')
            console.log('hello')
        }
       console.log(age)
       console.log(image)
    },[]);
*/
    const [redirect, setRedirect] = useState(false)
    let [form, setForm] = useState({
        user: '', age: '', genre: '', address: '', artist: '', part: '', image: ''
    })


    useEffect(async() => {
        const config = {
            headers: {
                'Content-Type': 'multipart/form-data',
                'X-CSRFToken': Cookies.get('csrftoken')
            }
        };
        const formData = new FormData()
        Object.entries(form).forEach(([key, value]) => {
            console.log(value)
            formData.append(key, value)
          })
        console.log(formData.get("age"));
        const response = await Axios.post('views/edit/', formData, config);
        console.log(response.data)
        if (response.data=="Edit!") {
            setRedirect(true)
        } else {
            console.log('fail')
        }
    },[form]);


    const doChangeAge = (e) => {
        setAge(e.target.value)
    }
    const doChangeGenre = (e) => {
        setGenre(e.target.value)
    }
    const doChangeAddress = (e) => {
        setAddress(e.target.value)
    }
    const doChangeArtist = (e) => {
        setArtist(e.target.value)
    }
    const doChangePart = (e) => {
        const arr1 = [];
        const part1 = document.form1.part;
        for (let i = 0; i<part1.length; i++) {
            if (part1[i].checked) {
                arr1.push(part1[i].value);
            }
        }
        setPart(arr1)
        //setPart({ ...part, [e.target.value]: e.target.checked });
    }
    const doChangeImage = (e) => {
        const file = document.getElementById('image').files[0];
        if (file !== {}) {
            setImage(file);
            setPreview(window.URL.createObjectURL(file));
            console.log('set')
        }
        else {
            setImage('')
            console.log('hello')
        }
    }

    const doSubmit = (e) => {
        setForm({pk: id, age:age, genre:genre, address:address, artist:artist, part:part, image:image})
        e.preventDefault()
    }

    if (redirect) {
        return <Redirect to="/" />
    }

    return(
            <div>
            <form onSubmit={doSubmit} name="form1">
            <div class="input-group mb-3">
                <label for="name" className="col-sm-2 col-form-label">写真</label>
                <input type="file" id="image" accept="image/*" onChange={doChangeImage} />
            </div>
            <img src={preview} class="img-thumbnail" />
            <div class="input-group mb-3">
                <div className="input-group-prepend">
                    <label className="input-group-text" for="age">年齢</label>
                    <input type="number" id="age" onChange={doChangeAge} value={age} required />
                </div>
            </div>

            <div class="input-group mb-3">
                <div className="input-group-prepend">
                    <label className="input-group-text" for="inputGroupSelect02">好きなジャンル</label>
                </div>
                <select required class="custom-select" id="inputGroupSelect02" onChange={doChangeGenre}>
                    <option>選択してください</option>
                    <option id="ロック" value="ロック">ロック</option>
                    <option id="ポップス" value="ポップス">ポップス</option>
                    <option id="クラシック" value="クラシック">クラシック</option>
                    <option id="ジャズ" value="ジャズ">ジャズ</option>
                    <option id="アニソン" value="アニソン">アニソン</option>
                </select>
            </div>

            <div class="input-group mb-3">
                <div className="input-group-prepend">
                    <label className="input-group-text" for="address">お住まい</label>
                    <input type="text" id="address" onChange={doChangeAddress} value={address} />
                </div>
            </div>

            <div class="input-group mb-3">
                <div className="input-group-prepend">
                    <label className="input-group-text" for="artist">好きなアーティスト</label>
                    <input type="text" id="artist" onChange={doChangeArtist} value={artist} />
                </div>
            </div>

            <fieldset className="form-group">
            <div class="row">
                <legend className="col-form-label col-sm-2 pt-0">パート</legend>
                <div className="col-sm-10">
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="part" id="ギター" value="ギター" onChange={doChangePart} />
                        <label className="form-chack-label" for="part1">ギター</label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="part" id="ベース" value="ベース" onChange={doChangePart} />
                        <label className="form-chack-label" for="part2">ベース</label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="part" id="ドラム" value="ドラム" onChange={doChangePart} />
                        <label className="form-chack-label" for="part3">ドラム</label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="part" id="ボーカル" value="ボーカル" onChange={doChangePart} />
                        <label className="form-chack-label" for="part4">ボーカル</label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="part" id="キーボード" value="キーボード" onChange={doChangePart} />
                        <label className="form-chack-label" for="part5">キーボード</label>
                    </div>
                </div>
            </div>
            </fieldset>
            <input type="submit" className="btn btn-primary" value="Click" />
            </form>
            </div>
    ) 
}