import React, {useState, useEffect} from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom';
import Cookies from 'js-cookie';
import {Axios} from '../Axios'

export default function Auth() {
    const url = 'http://127.0.0.1:8000/views/authenticated'
    const [redirect_login, SetRedirect_Login] = useState(false);

    useEffect(async() => {
        const config = {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'X-CSRFToken': Cookies.get('csrtoken')
            }
        };
        console.log(Cookies.get('csrftoken'))
        const res_auth = await Axios.post('views/authenticated/', config);
        console.log(res_auth)
        if (res_auth.data=='failed') {
            SetRedirect_Login(true)
        }
    })

    if (redirect_login) {
        return <Redirect to='/login' />
    }

    return (
        <div>
        </div>
    )
    }